#contact {
  height: 100vh;
  user-select: none; /* 텍스트 선택 불가 */
  -webkit-user-drag: none; /* 드래그 비활성화 */
  -webkit-user-select: none; /* Safari 텍스트 선택 방지 */
  pointer-events: auto; /* 필요 시 요소 상호작용 유지 */
}
.contact__inner {
  width: 100%;
  height: 100%;
  padding: 16px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  flex-direction: column;

  // @media (max-width: 800px) {
  // 	justify-content: center;
  // }

  .contact__title {
    font-size: 10vw;
    text-transform: uppercase;
    line-height: 1;
    font-weight: 800;
    white-space: nowrap;
    text-indent: -0.5vw;
    letter-spacing: -0.3vw;
  }

  .animated-text {
    position: relative;
    height: 100vh;
  }
  .contact__text {
    position: relative; /* 부모 요소 기준으로 자식 요소 배치 */
    width: 100%;
    height: 500px;
    background-color: var(--mainColor);
    color: var(--mainBackground);

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: visible;
    @media (max-width: 800px) {
      font-size: 10px;
    }

    cursor:
      url('https://img.icons8.com/emoji/48/backhand-index-pointing-up-emoji.png') 56 56,
      auto;

    .word {
      position: absolute; /* 위치를 절대값으로 설정 */
      padding: 5px 15px;
      border-radius: 50px;
      cursor:
        url('https://img.icons8.com/emoji/48/backhand-index-pointing-up-emoji.png') 56 56,
        auto;
      background-color: var(--white);
      font-size: 1em;
      color: var(--mainColor);
      transition:
        opacity 0.5s ease,
        transform 0.1s ease-out; /* 트랜지션 효과 */
      z-index: 10;
    }

    .highlighted {
      cursor:
        url('https://img.icons8.com/emoji/48/backhand-index-pointing-up-emoji.png') 56 56,
        auto;
      background-color: #423fff;
      color: #fff;
      font-weight: 700;
      padding: 10px 25px;
      font-size: 1.5em;
    }
    .text {
      font-size: 3vw;
      line-height: 1;
      font-weight: 900;
      text-transform: uppercase;
      text-decoration: underline;
      text-align: center;
      position: relative;
      z-index: 100;
      transition: all 0.3s;
      cursor: all-scroll;

      // @media (max-width: 800px) {
      // 	font-size: 24px;
      // 	padding: 0 16px;
      // 	word-break: break-all;
      // }
    }
  }
  .contact__lines {
    width: 100%;

    .line {
      display: block;
      width: 100%;
      height: 1px;
      background-color: var(--mainColor);
      margin-bottom: 0.5vw;

      &:nth-child(1) {
        height: 25px;
      }
      &:nth-child(2) {
        height: 17px;
      }
      &:nth-child(3) {
        height: 13px;
      }
      &:nth-child(4) {
        height: 9px;
      }
      &:nth-child(5) {
        height: 5px;
      }
      &:nth-child(6) {
        height: 2px;
      }
      &:nth-child(7) {
        height: 1px;
      }
    }

    &.top {
      .line {
        margin-top: 0.5vw;
        margin-bottom: 0;
        display: none;

        @media (max-width: 800px) {
          display: block;
        }

        &:nth-child(1) {
          height: 1px;
        }
        &:nth-child(2) {
          height: 2px;
        }
        &:nth-child(3) {
          height: 5px;
        }
        &:nth-child(4) {
          height: 9px;
        }
        &:nth-child(5) {
          height: 13px;
        }
        &:nth-child(6) {
          height: 17px;
        }
        &:nth-child(7) {
          height: 25px;
        }
      }
    }
  }
}
.word {
  position: absolute;
  padding: 5px 10px;
  background: #f0f0f0;
  color: #fff;

  border-radius: 8px;
  text-align: center;
  top: 50%; /* 초기 위치 */
  left: 50%; /* 초기 위치 */
  transform: translate(-50%, -50%); /* 중앙 정렬 */
  z-index: 1000; /* 충돌 방지 */
}
.number {
  font-family: var(--mainNum-font) !important;
}
